<template>
    <div id="workshop" v-if="fields.title != null">
        <b-row>
            <b-col>
                <h2>{{ fields.title }}</h2>
            </b-col>
        </b-row>
        <b-row>
            <b-col sm="12">
                <prismic-rich-text class="text-center" :field="fields.description"></prismic-rich-text>
            </b-col>
            <b-col v-for="item of fields.pictures">
                <b-img fluid-grow :src="item.picture.url" :alt="item.picture.alt"></b-img>
            </b-col>
        </b-row>
        <b-row>
            <b-col>
                <prismic-rich-text :field="fields.text"></prismic-rich-text>
            </b-col>
        </b-row>
        <b-row>
            <b-col>
                <h2>Decouvrez l'origine de nos pierre précieuses</h2>
            </b-col>
        </b-row>
        <b-row class="flex-row flex-wrap align-items-center justify-content-between">
            <div v-for="(jewel, index) in jewels"
                 class="jewel"
                 :key="index" @hover="onJewelSelection(jewel)"
                 v-bind:class="{'j-selected': jewel.id === jewelSelected}"
                 @click="onJewelSelection(jewel)" v-b-tooltip.hover :title="jewel.title">
                <b-img center :src="jewel.image.path" fluid/>
            </div>
        </b-row>
        <b-row v-if="jewelSelected">
            <b-col>
                <h3>{{ getSelectedJewel.title }}</h3>
                <p>{{ getSelectedJewel.description }}</p>
            </b-col>
        </b-row>

        <b-row>
            <b-col>
                <h2>Découvrez note atelier</h2>
            </b-col>
        </b-row>

        <b-row>
            <b-col class="flex-fill align-content-center justify-content-center">
                <b-embed
                        type="iframe"
                        aspect="16by9"
                        src="https://www.youtube.com/embed/tHOGikxOuSk?rel=0"
                        allowfullscreen
                ></b-embed>
            </b-col>
        </b-row>

    </div>
</template>

<script>
    export default {
        name: 'Home',
        data: () => ({
            jewelSelected: 3,
            jewels: [
                {
                    id: 1,
                    title: 'L\'or',
                    description: 'Utilisé depuis des millénaires pour la monnaie, la décoration et la joaillerie, l\'or est originellement jaune doré. En orfèvrerie, on en varie la couleur et on augmente la dureté en l\'alliant à d\'autres métaux. L\'addition d\'argent, de platine donne de l\'or blanc. Le cuivre, de l\'or rose ou rouge. La pureté de l\'or se définit par la proportion d\'or pur, natif présent, et elle s\'exprime en carats. Cette pureté varie de 9 carats (37.5% au moins d\'or pur) à 24 carats (100% d\'or pur) en passant par le 14, 18 et 22. En France l\'or le plus utilisé est le 18 carats (75% d\'or pur) comme l\'atteste son poinçon à tête d\'aigle de l\'Etat français.',
                    image: {
                        path: 'http://www.landreau.fr/images_communes/connaissance01.jpg'
                    }
                },
                {
                    id: 2,
                    title: 'L\'argent',
                    description: 'D\'une couleur caractéristique, d\'un blanc argent brillant, et d\'un éclat métallique, l\'argent se trouve ordinairement sous forme massive. Son poinçon français est une tête de minerve. Facilement oxydable il demande à être nettoyé voir poli régulièrement..',
                    image: {
                        path: 'http://www.landreau.fr/images_communes/connaissance02.jpg'
                    }
                },
                {
                    id: 3,
                    title: 'Le diamant',
                    description: 'Il est le plus dur des minéraux. Fait de carbone pur, le diamant incolore est le plus prisé de toutes les gemmes. Les autres variétés (jaune jonquille, brune, verte, bleue, rose, rouge, grise ou noire) doivent leur couleur à d’autres composants chimiques. Mais c’est grâce au génie de l’homme et à la perpétuelle perfection de sa taille que le diamant se pare de ses feux enveloppants. Aujourd’hui encore, ce cristal naturel ne peut être taillé et poli que par lui-même. Pour évaluer sa qualité et sa beauté, il existe 4 critères reconnus dans le monde entier : les 4 C.',
                    image: {
                        path: 'http://www.landreau.fr/images_communes/connaissance03.jpg'
                    }
                },
                {
                    id: 4,
                    title: 'L\'amethyste',
                    description: 'Ce cristal de quartz de ton pourpre, lilas ou mauve est comme le veut son étymologie, la pierre qui protège de l’ivresse, fait gagner en tempérance et en sérieux. L’améthyste vient essentiellement d’Amérique du Sud et de Madagascar, mais on en trouve également en Russie et en Inde. Plus modestement, on peut même en extraire en Auvergne et dans les Alpes.',
                    image: {
                        path: 'http://www.landreau.fr/images_communes/connaissance04.jpg'
                    }
                },
                {
                    id: 5,
                    title: 'La citrine',
                    description: 'Variété de Quartz jaune doré, elle tient son nom du citron bien que sa couleur douce et chaude l’évoque assez peu. Depuis les temps anciens, on chauffe couramment le quartz améthyste ou une citrine trop légèrement jaune pour obtenir la belle couleur orangée typique. Les meilleures pierres se trouvent au Brésil, en Espagne ou à Madagascar.',
                    image: {
                        path: 'http://www.landreau.fr/images_communes/connaissance05.jpg'
                    }
                },
                {
                    id: 6,
                    title: 'Le saphir',
                    description: 'Le saphir est traditionnellement bleu, avec une palette infinie selon son origine géographique : de l’outremer le plus insondable, au ciel le plus pâle. Mais le saphir recueille sous son nom toutes les autres couleurs de la famille Corindon : le jaune, le rose, le vert, l’orangé. Seul lui échappe le rouge qui prend alors le nom de rubis. Provenant essentiellement de Thaïlande, du Sri Lanka, de Birmanie et d’Australie, les cristaux bruts sont extraits, chauffés pour en fixer la couleur, puis taillés pour enfin révéler leurs rayons de lumière.',
                    image: {
                        path: 'http://www.landreau.fr/images_communes/connaissance06.jpg'
                    }
                },
                {
                    id: 7,
                    title: 'L\'emeraude',
                    description: 'S’il est une pierre dont le nom est synonyme de couleur, c’est l’émeraude. Ne dit-on pas "vert émeraude", une "mer émeraude" ? Rarement sans inclusion, sa limpidité laisse entrevoir comme des herbes folles dessinant de subtils "jardins". Les plus belles proviennent de Colombie ensuite viennent celles du Brésil et d’Afrique. Issues d’un milieu liquide, elles sont difficilement extraites. On les plonge ensuite dans un bain d’huile incolore et naturel pour éviter qu’elles ne se dessèchent et deviennent friables. C’est la plus délicate des pierres précieuses, comme les femmes qui aiment les porter.',
                    image: {
                        path: 'http://www.landreau.fr/images_communes/connaissance07.jpg'
                    }
                },
                {
                    id: 8,
                    title: 'Le peridot',
                    description: 'C’est une pierre d’un vert-olive ou bouteille qui n’existe que dans une seule couleur. Son touché soyeux en fait une pierre unique. Avant que la Birmanie ne livre les plus beaux péridots actuels, cette gemme ne provenait que de la Mer Rouge.',
                    image: {
                        path: 'http://www.landreau.fr/images_communes/connaissance08.jpg'
                    }
                },
                {
                    id: 9,
                    title: 'L\'algue marine',
                    description: 'Sœur cadette de l’émeraude, faisant donc partie de la même famille des Béryls, c’est la pierre fine qui porte le mieux son nom. Elle offre une infinie variété de bleus allant de l’à peine discernable, au bleu des plus belles Mers du sud comme les aigues-marines "Santa Maria" du Brésil que l’on ne trouve plus aujourd’hui. Outre du Brésil, elles proviennent aussi de Madagascar, d’Afrique ou d’Inde. Sa limpidité en fait son plus grand atout.',
                    image: {
                        path: 'http://www.landreau.fr/images_communes/connaissance09.jpg'
                    }
                },
                {
                    id: 10,
                    title: 'Le topaz',
                    description: 'Contrairement aux idées reçues, la topaze a une très vaste gamme de couleurs. Les topazes roses ou d’un jaune doré profond (Topaze Impériale) sont les plus appréciées. Les bleues et les vertes sont également estimées. C’est la plus brillante et dure des pierres fines. On la trouve surtout au Brésil, en Afrique, à Madagascar au Sri Lanka et en Russie.',
                    image: {
                        path: 'http://www.landreau.fr/images_communes/connaissance10.jpg'
                    }
                },
                {
                    id: 11,
                    title: 'Le rubis',
                    description: 'La fascinante couleur rouge du rubis est la plus rare du monde minéral. Il étale sa palette de couleur du rouge sang incandescent, en rouge tendre, framboise, frêle frontière avec le rose parfait du saphir. Les plus beaux rubis viennent de Birmanie, Thaïlande ou Viêt Nam où ils subissent le même processus d’embellissement que les saphirs. C’est par excellence une pierre du soir.\n',
                    image: {
                        path: 'http://www.landreau.fr/images_communes/connaissance11.jpg'
                    }
                },
            ],
            fields: {
                title: null,
                description: null,
                pictures: null,
                text: null,
                jewels: null
            }
        }),
        computed: {
            getSelectedJewel() {
                return this.jewels.find(jewel => this.jewelSelected === jewel.id)
            }
        },
        methods: {
            onJewelSelection(jewel) {
                this.jewelSelected = jewel.id
            },
            getContent() {
                this.$prismic.client.getSingle('workshop')
                    .then((document) => {
                        this.fields.title = document.data.title;
                        this.fields.description = document.data.description;
                        this.fields.pictures = document.data.pictures;
                        this.fields.text = document.data.text;
                        this.fields.jewels = document.data.jewel;
                    })
            }
        },
        created() {
            this.getContent();
        }
    }
</script>
